import Introduce from './docs/introduce.md'
import CommonStyle from './docs/common-style.md'
import Toolbar from './docs/toolbar.md'
import BaseTree from './docs/base-tree.md'
import ModalTree from './docs/modal-tree.md'
import BasedataModalTree from './docs/basedata-modal-tree.md'
import SelectTree from './docs/select-tree.md'
import InputNumber from './docs/input-number.md'
import Subject from './docs/subject.md'

const routes = [
  {
    path: '/',
    component: Introduce,
    name: 'default'
  },
  {
    path: '/introduce',
    name: 'introduce',
    component: Introduce
  },
  {
    path: '/common-style',
    name: 'common-style',
    component: CommonStyle
  },
  {
    path: '/toolbar',
    name: 'toolbar',
    component: Toolbar
  },
  {
    path: '/base-tree',
    name: 'base-tree',
    component: BaseTree
  },
  {
    path: '/modal-tree',
    name: 'modal-tree',
    component: ModalTree
  },
  {
    path: '/basedata-modal-tree',
    name: 'basedata-modal-tree',
    component: BasedataModalTree
  },
  {
    path: '/select-tree',
    name: 'select-tree',
    component: SelectTree
  },
  {
    path: '/input-number',
    name: 'input-number',
    component: InputNumber
  },
  {
    path: '/subject',
    name: 'subject',
    component: Subject
  }
]

export default routes



